import { Link } from "react-router-dom";
function Menu() {
  return (
    <div className="menu">
      <ul className="menu_list">
        <li className="menu_list-icon">
          <Link to="/portfolio">
            <img src="imgs/info.png" alt="info" />
          </Link>
        </li>
        <li className="menu_list-icon">
          <Link to="/projects">
            <img src="imgs/project-management.png" alt="projects" />
          </Link>
        </li>
        <li className="menu_list-icon">
          <img src="imgs/send.png" alt="contact" />
        </li>
      </ul>
    </div>
  );
}

export default Menu;
